.project img {
  max-width: 100%;
  margin: 1rem 0;
}


body {
    font-family: "Open Sans", sans-serif;
    font-size: large;
    margin: 0;
    padding: 0;
  }
  
  .main-section {
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(to right, #FFC371, #FF5F6D);
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  
  .main-section h1 {
    color: #fff;
    font-size: 2.5rem;
    font-weight: bold;
    margin: 0;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-shadow: 1px 1px #FF5F6D;
  }
  
  .main-section p {
    color: #f5f4f4;
    font-size: 1.2rem;
    margin: 2rem 0;
    text-shadow: 1px 1px #FF5F6D;
  }
  
  .main-section button {
    padding: 1rem 2rem;
    border: 2px solid #9c1717;
    border-radius: 10px;
    background-color: transparent;
    color: #c31d1d;
    font-size: 1.2rem;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: all 0.3s ease;
    cursor: pointer;
  }
  
  .main-section button:hover {
    background-color: #932b2b;
    color: #333;
  }
  
  .container {
    max-width: 1100px;
    margin: 0 auto;
    padding: 2rem;
  }
  @media only screen and (max-width: 767px) {
    .container {
      width: 97%;
      padding: 0.5rem;
    }
  }
  
  header {
    background-color: rgba(96, 200, 108, 0.677);
    color: #0b0b0b;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
  }
  
  header .title {
    margin: 0;
  }
  
  .projects {
    display: flex;
    flex-wrap: wrap;
  }
  
  .project {
    width: 100%;
    margin-bottom: 2rem;
  }
  
  .project img {
    width: 100%;
    height: auto;
  }
  
  .project h3 {
    font-size: 1.5rem;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-top: 1rem;
  }
  
  footer {
    background-color: #afcc28e0;
    color: #0e0d0d;
    display: flex;
    justify-content: center;
    font-size: 1.2rem;
  }
  
  .skills-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .skill {
    width: 30%;
    text-align: center;
    margin-bottom: 30px;
  }
  
  .skill-name {
    font-size: 1.2em;
    margin-bottom: 10px;
  }
  
  .skill-level {
    height: 10px;
    background-color:white;
    border-radius: 5px;
    position: relative;
    margin-bottom: 10px;
  }
  
  .skill-level-bar {
    height: 100%;
    background-color: lightblue;
    border-radius: 5px;
    position: absolute;
    left: 0;
    top: 0;
  }
  
  .skill-level-value {
    font-size: 0.8em;
  }
  nav {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    background-color: #fff;
    border-radius: 20px;
    overflow: hidden;
  }
  
  nav a {
    color: #fff;
    text-decoration: none;
    margin: 0 1rem;
    font-size: 1.2rem;
    font-weight: bold;
    transition: all 0.3s ease;
    white-space: nowrap;
  }
  
  nav i {
    color: white;
    transition: all 0.3s ease;
    font-size: 1.2rem;
  }
  
  nav i:hover {
    transform: translateY(-5px);
  }
  
  @media (max-width: 600px) {
    nav {
      flex-direction: column;
    }
  
    nav a, nav i {
      font-size: 1rem;
      margin: 0.5rem 0;
    }
  }
  
  #about {
    padding: 5px;
  }
  
  #about h2 {
    font-size: 26px;
    margin-bottom: 20px;
    font-weight: bold;
    text-align: center;
  }
  
  #about p {
    font-size: 18px;
    line-height: 1.5;
    margin-bottom: 20px;
    text-align: left;
  }
  
  .experience {
    margin-bottom: 30px;
    background-color: #fff;
  }
  
  .experience-company {
    margin-bottom: 10px;
    font-weight: bold;
    color: #333;
  }
  
  .experience-details {
    margin-bottom: 10px;
    font-style: italic;
    color: #777;
  }
  
  .experience-title {
    font-weight: bold;
  }
  
  .experience-description {
    margin-bottom: 20px;
    color: #333;
    font-size: unset;
  }
  
  .buttons {
    margin-top: 20px;
    display: flex;
    justify-content: center;
  }
  
  .spacer {
    width: 20px;
    height: 0;
  }
  
  .experience a {
    padding: 10px 20px;
    background-color: #a47777;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    margin-left: 10px;
    margin-right: 10px;
  }
  
  .experience a:hover {
    background-color: #753f3f;
  }
  .download-resume {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .download-resume-button {
    background-color: black;
    color: white;
    padding: 8px 15px;
    border-radius: 5px;
    text-decoration: none;
  }
  
  .github-button {
    display: inline-block;
    padding: 10px 15px;
    background-color: #4d3ead;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    font-weight: bold;
    margin-top: 10px;
  }
  .containers {
    display: flex;
    flex-direction: column;
  }
    
  h2.education-table {
    margin-top: 50px;
    border-collapse: collapse;
  }
    
  .education-table thead {
    background-color: #0e0e0e;
    color: #fff;
  }
    
  .education-table th,
  .education-table td {
    padding: 12px;
    text-align: center;
  }
  
  .education-header {
    padding: 0;
    display: block;
    font-size: 1.5em;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
  }
  
  @media only screen and (max-width: 600px) {
    .education-table {
      font-size: 14px;
      max-width: 95%;
    }
  
    .education-table th,
    .education-table td {
      padding: 8px;
    }
  }
  @media (max-width: 768px) {
    .p1 {
      display: none;
    }
  }
    form {
      background-color: #fff;
      padding: 3.5rem;
      border-radius: 10px;
      box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    }
    
    form input, form textarea {
      width: 100%;
      padding: 1rem;
      font-size: 1.2rem;
      margin-bottom: 2rem;
      border: none;
      border-radius: 5px;
      box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
      transition: all 0.3s ease;
    }
    
    form input:focus, form textarea:focus {
      outline: none;
      box-shadow: 0 15px 30px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    }
      .alert {
      padding: 10px;
      margin-bottom: 20px;
      border-radius: 5px;
      display: flex;
      justify-content: center;
      }
    
    .alert-success {
      background-color: #dff0d8;
      border-color: #3c763d;
      color: #3c763d;
    }
    
    .alert-danger {
      background-color: #f2dede;
      border-color: #a94442;
      color: #a94442;
    }
    @media (max-width: 767px) {
       form{      
      padding: 2.5rem;
       }
      form input, form textarea {
        font-size: 1.1rem;
      }
    }
    